@charset "UTF-8";
@font-face {
  font-family: "BebasNeueBold";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/BebasNeueBold.woff") format("woff"), url("../fonts/BebasNeueBold.woff2") format("woff2"); }

@font-face {
  font-family: "BebasNeueLight";
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/BebasNeueLight.woff") format("woff"), url("../fonts/BebasNeueLight.woff2") format("woff2"); }

@font-face {
  font-family: "OpenSans";
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/OpenSans-Light.woff") format("woff"), url("../fonts/OpenSans-Light.woff2") format("woff2"); }

.header {
  display: flex;
  font-family: "OpenSans", "Arial Black", sans-serif;
  justify-content: space-between;
  padding-right: 100px;
  padding-left: 100px;
  box-sizing: border-box;
  max-width: 1440px; }

.header__nav-tab {
  display: none; }

.header__nav {
  display: flex; }
  .header__nav ul {
    align-self: center;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    max-width: 440px; }
    .header__nav ul li:nth-of-type(n+1) {
      margin-right: 20px; }
    .header__nav ul li:nth-of-type(4n) {
      margin-right: 0; }
    .header__nav ul li:nth-of-type(n+5) {
      margin-top: 20px; }

.header__nav-button {
  border: none;
  font-family: "BebasNeueBold", "Arial Black", sans-serif;
  font-size: 25px;
  cursor: pointer;
  background-color: transparent;
  color: #000000;
  transition: color 0.3s ease; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .header__nav-button:hover, .header__nav-button:focus {
      color: #db5800; } }
  .header__nav-button--selected {
    color: #b31515; }

.footer {
  display: flex;
  flex-direction: column;
  font-family: "OpenSans", "Arial Black", sans-serif;
  padding-right: 100px;
  padding-left: 100px;
  box-sizing: border-box;
  max-width: 1440px;
  margin-top: 50px;
  margin-bottom: 50px; }

.footer__copyright {
  display: flex;
  align-self: flex-end; }
  .footer__copyright span {
    font-family: "BebasNeueLight", "Arial Black", sans-serif;
    font-size: 20px;
    margin-left: 20px;
    color: #000000; }
  .footer__copyright h3 {
    font-family: "BebasNeueLight", "Arial Black", sans-serif;
    margin: 0;
    font-size: 20px;
    color: #000000; }
    .footer__copyright h3 a {
      text-decoration: none;
      color: #db5800; }

.main {
  display: flex;
  flex-direction: column;
  font-family: "OpenSans", "Arial Black", sans-serif;
  padding-right: 100px;
  padding-left: 100px;
  box-sizing: border-box;
  max-width: 1440px; }
  .main h1 {
    font-family: "BebasNeueBold", "Arial Black", sans-serif;
    font-size: 50px;
    align-self: flex-start; }

.main__reset {
  border: none;
  background-color: transparent;
  width: 140px;
  padding: 0;
  font-size: 20px;
  font-family: "BebasNeueLight", "Arial Black", sans-serif;
  cursor: pointer;
  position: relative; }
  .main__reset::before {
    content: "";
    position: absolute;
    top: 0;
    right: -20px;
    background-image: url("../img/icon-cross.svg");
    background-repeat: no-repeat;
    background-size: 15px 15px;
    width: 15px;
    height: 15px;
    opacity: 0;
    transition: opacity 0.3s ease; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .main__reset:hover::before, .main__reset:focus::before {
      color: #db5800;
      opacity: 0.9; } }

.main__catalog {
  display: flex;
  flex-direction: column; }

.main__catalog-category {
  display: none;
  overflow: hidden;
  flex-direction: column;
  max-width: 750px;
  align-self: center;
  position: relative; }
  .main__catalog-category ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex; }
  .main__catalog-category--visible {
    display: flex; }

.main__catalog-category-pages {
  margin-top: 50px;
  margin-bottom: 50px;
  align-self: center;
  position: absolute;
  bottom: 0;
  left: 0; }

.pages {
  background-color: transparent;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border-color: #b31515;
  cursor: pointer;
  transition: opacity 0.3s ease;
  transition: background-color 0.3s ease;
  opacity: 0.9;
  padding: 0;
  margin: 0;
  position: absolute; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .pages:hover, .pages:focus {
      background-color: #db5800; } }
  .pages--inactive {
    pointer-events: none;
    background-color: #b31515;
    opacity: 0.5; }

#leftControl {
  bottom: -50px;
  left: 340px; }

#rightControl {
  bottom: -50px;
  right: -410px; }

#slideshow #slidesContainer {
  width: 770px;
  height: 920px;
  overflow: auto;
  /* разрешаем прокрутку */
  position: relative; }

#slideshow #slidesContainer .slide {
  width: 750px;
  height: 920px; }

.choice {
  display: flex; }

.choice__slide {
  display: flex;
  flex-direction: column;
  max-width: 350px; }
  .choice__slide h2 {
    font-family: "BebasNeueBold", "Arial Black", sans-serif;
    font-size: 40px;
    text-align: center;
    min-height: 80px;
    align-self: center; }
  .choice__slide span {
    font-family: "BebasNeueLight", "Arial Black", sans-serif;
    font-size: 20px;
    text-align: left; }
  .choice__slide p {
    font-family: "BebasNeueLight", "Arial Black", sans-serif;
    font-size: 20px;
    text-align: left; }
  .choice__slide:nth-of-type(2n) {
    margin-left: 50px; }

.choice__year {
  margin-top: 15px; }
